*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: rgb(37, 37, 37);
}

.wrapper{
  margin-left: 145px;
  margin-right: 145px;
}

.flex-container{
  display: flex;
}

header {
  position: relative;
  width: 100%;
  max-width: 100vw;
  height: 100%;
  min-height: 70vh;
  max-height: 70vh;
  background-image: url('assets/fondo-header.png');
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

header::before{
  content:'';
	position: absolute;
  top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(0,0,0,0.3);
}

nav {
  position: absolute;
  right: 0;
  top: 0;
}

ol{
  display: flex;
  list-style: none;
  margin-top: 25px;
  margin-right: 30px;
}

header nav ol li{
  padding-left: 78px;
  font-family: 'Oswald', sans-serif;
  font-size: 1.2rem;
  font-weight: 200;
}

header nav ol li:hover {
  text-decoration: line-through;
}

a{
  text-decoration: none;
  color: white;
}

h1{
  font-family: 'Oswald', sans-serif;
  font-size: 6rem;
}

h2{
  font-family: 'Playfair Display', sans-serif;
  font-weight: 800;
  font-size: 2rem;
}

h4{
  font-family: 'Oswald', sans-serif;
  font-size: 1rem;
  font-weight: 200;
}

p{
  font-family: 'Oswald', sans-serif;
  font-size: 1.5rem;
  font-weight: 100;
}

#coffee-text {
  color: #FFF500;
}

#intro-content{
  margin-top: auto;
  margin-bottom: auto;
  margin-top: 10%;
  position: absolute;
  left: 0;
}

#intro-content p {
  margin-top: -15px;
  text-align: left;
  font-family: 'Oswald', sans-serif;
  font-size: 2rem;
  font-weight: 200;
}

#cardsContainer{
  margin-top: 50px;
  justify-content: space-between;
  margin-bottom: 50px;
}

figure.cardsStyle {
  position: relative;
  width: 350px;
  height: 100%;
  max-height: 380px;
  min-height: 380px;
}

.cardsImages{
  width: 100%;
  height: 100%;
  min-width: 350px;
  max-width: 350px;
  min-height: 427px;
  max-height: 427px;
}

.cardsStyle{
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cardsStyle p {
  width: 75%;
  font-size: 1.3rem;
  position: absolute;
}

.cardsStyle button {
  font-family: 'Oswald', sans-serif;
  font-weight: 300;
  font-size: 1rem;
  position: absolute;
  margin-top: 120px;
  outline: none;
  background-color: transparent;
  border: 2px solid white;
  padding: 5px 50px;
  border-radius: 5px;
  color: white;
}

#boxesContainer {
  margin-top: 150px;
  margin-bottom: 125px;
  width: 100%;
  height: auto;
}

.boxesStyles{
  margin-bottom: 150px;
}

.boxesStyles p{
  width: 100%;
  max-width: 1020px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}

.boxesStyles h2{
  width: 100%;
  max-width: 550px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}

.postsStyles figure img {
  width: 100%;
  height: 100%;
  min-width: 550px;
  max-width: 550px;
  min-height: 380px;
  max-height: 380px;
}

.postsTextStyles {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.postsTextStyles {
  position: relative;
  width: 100%;
  margin: 0 50px;
  font-size: 1.4rem;
}

.postsTextStyles h2 {
  width: 100%;
  text-align: center;
}

.postsTextStyles p {
  margin-top: 10px;
}

.postsTextStyles button {
  outline: none;
  background-color: #FFF500;
  padding: 10px 50px;
  font-family: 'Oswald', sans-serif;
  font-weight: 400;
  font-size: 1rem;
  border: 0;
  border-radius: 5px;
  margin-top: 10px;
}

footer{
  margin-top: 100px;
  position: relative;
  background-color: black;
  width: 100%;
  height: 100%;
  min-height: 175px;
  max-height: 175px;
  justify-content: space-between;
}

footer span {
  font-size: 25px;
  padding-right: 20px;
}


.listFooter{
  flex-direction: column;
  text-align: left;
  padding-bottom: 20px;
}

.listFooter li {
  font-family: 'Playfair Display', sans-serif;
  font-size: 1rem;
  font-size: 400;
}

#secondSectionFooter{
  position: relative;
}

#formularioOffers{
  flex-direction: column;
  margin-top: 25px;
}

#formularioOffers label {
  color: #DFE300;
  font-size: 1.3rem;
  padding-bottom: 5px;
  font-family: 'Oswald', sans-serif;
}

#formularioOffers input{
  outline: none;
  width: 100%;
  height: 100%;
  min-height: 30px;
  max-height: 30px;
  max-width: 450px;
  border-radius: 5px;
  padding-left: 15px;
}

#copyright {
  position: absolute;
  right: 0;
  bottom: 0;
  padding-bottom: 20px;
}

.App {
  text-align: center;
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
